import { Api } from './../../Api/Api';
const CategoriesStore = {
    namespaced: true,
    state: {
        data: [],
    },
    getters: {
        categories: ({ data }) => (data),
    },
    mutations: {},
    actions: {
        getAllCategories(context) {
            return Api.categoriesApi.getAllCategories()
                .then(response => {
                    context.state.data = response;
                    console.log('get categories!', context.state.data);
                })
                .catch(e => console.error(e));
        },
        addNewCategory(context, newCategory) {
            return new Promise(resolve => {
                Api.categoriesApi.addNewCategory(newCategory)
                    .then(response => {
                        console.log(response);
                        resolve();
                    })
                    .catch(e => console.error(e));
            });
        },
        editCategory(context, editedCategory) {
            return new Promise(resolve => {
                Api.categoriesApi.editCategory(editedCategory)
                    .then(response => {
                        console.log(response);
                        resolve();
                    })
                    .catch(e => console.error(e));
            });
        },
        removeCategory(context, id) {
            return new Promise(resolve => {
                Api.categoriesApi.removeCategory(id)
                    .then(response => {
                        console.log(response);
                        resolve();
                    })
                    .catch(e => console.error(e));
            });
        },
    },
};
export default CategoriesStore;



