<template lang="pug">
    #app
        app-header
        router-view
        app-footer
</template>

<script>
import AppHeader from './components/_common/header';
import AppFooter from './components/_common/footer';
import { mapGetters, mapActions } from 'vuex';
export default {
    name: 'app',
    methods: {
        ...mapActions('СategoriesStore', ['getAllCategories']),
    },
    created() {
        this.getAllCategories();
    },
    components: {
        AppHeader,
        AppFooter,
    }
};
</script>



