import { Api } from './../../Api/Api';
import { store } from '../index';

const CategoriesStore = {
    namespaced: true,
    state: {
        data: [],
    },
    getters: {
        posts: ({ data }) => (data),
    },
    mutations: {},
    actions: {
        getAllPostsPreview(context) {
            console.log(store.state.token);
            return Api.postsApi.getAllPostsPreview(store.state.token)
                .then(response => {
                    context.state.data = response;
                })
                .catch(e => console.error(e));
        },
        getPostsByCategory(context, category) {
            return Api.postsApi.getPostsByCategory(category)
                .then(response => {
                    context.state.data = response;
                })
                .catch(e => console.error(e));
        },
        getLastPosts(context, count) {
            return Api.postsApi.getLastPostsInAllCategories(count)
                .then(response => {
                    context.state.data = response;
                })
                .catch(e => console.error(e));
        },
        getPostById(context, id) {
            console.log(id);
            return Api.postsApi.getPostById(id)
                .then(response => {
                    context.state.data = response;
                })
                .catch(e => console.error(e));
        },
        addNewPost(context, post) {
            return Api.postsApi.addNewPost(post)
                .then(response => { console.log(response); return; })
                .catch(e => console.error(e));
        },
        editPost(context, data) {
            return Api.postsApi.editPost(data.formData, data.id)
                .then(response => { console.log(response); return; })
                .catch(e => console.error(e));
        },
        removePost(context, id) {
            return Api.postsApi.removePost(id)
                .then(response => { console.log(response); return; })
                .catch(e => console.error(e));
        },
    },
};
export default CategoriesStore;



