<template lang="pug">
  div
    b-tabs.mt-3
        b-tab(title='Рубрики' v-if='thisUser.role === "Admin"')
            create-categories()
        b-tab(title='Новости')
            create-post()
        b-tab(title='Авторы' v-if='thisUser.role === "Admin"')
            create-author()
</template>

<script>
import { mapGetters } from 'vuex';

export default {
    computed: {
        ...mapGetters('UsersStore', ['thisUser'])
    },
    components: {
        createCategories: require('./categories'),
        createAuthor: require('./createAuthor'),
        createPost: require('./posts')
    }
}
</script>




