<template lang="pug">
    b-container
        b-row.posts-panel
            b-col(cols="12" md="6" order="2" order-md="1")
                small(v-if="mode === 'edit'") Вы редактируете запись
                small(v-else-if="mode === 'create'") Вы пишите новую запись
                b-form(enctype="multupart/form-data")
                    b-form-group(
                        label="Заголовок:"
                        label-for="postTitle"
                        description="Введите заголовок новости")
                        b-form-input(
                            id="postTitle"
                            type="text"
                            v-model="newPost.title"
                            required
                            placeholder="Заголовок")

                    b-form-group(
                        label="Постер:"
                        label-for="postPoster"
                        description="Добавьте постер новости")
                        b-form-file(
                            id="postPoster"
                            v-model="newPost.picture"
                            choose-label="Загрузить"
                            accept=".jpg, .jpeg, .png"
                            ref="poster"
                            required)

                    b-form-group(
                        label="Укажите основную рубрику"
                        label-for="category"
                        description="Все рубрики")
                        b-form-select(
                            id="category"
                            v-model="newPost.category" :options="categoriesOptions"
                            required)

                    b-form-group(
                        label="Укажите подрубрику (необязательно)"
                        label-for="category"
                        :description="'Подрубрики ' + newPost.category.title"
                        v-if="newPost.category")
                    b-form-select(
                        id="category"
                        v-model="newPost.subcategory"
                        :options="subcategoriesTitles(newPost.category)")
            b-col(cols="12" md="6" order="1" order-md="2")
                news-list(
                    @choosePost="choosePost"
                    @deletePost="deletePost"
                    )
        b-row
            b-col
                b-form-group(
                    label="Содержимое:"
                    label-for="postTitle"
                    description="Введите содержимое новости"
                    )
                    wysiwyg(v-model="newPost.HTML")

                b-form-group(
                    label="Шорткат:"
                    label-for="postShortcat"
                    description="Добавьте шорткат (краткое описание)")
                    b-form-textarea(
                        v-model="newPost.shortcat"
                        placeholder="Краткое описание..."
                        :rows="4"
                        :max-rows="4"
                        :no-resize="true")
                b-button(type="submit" @click="submitPost") Опубликовать
        b-modal(ref="alertModal" hide-footer) {{ infoAlert }}
</template>

<script>
import cyrToLat from "../../jsModules/cyrToLat";
import { mapGetters, mapActions } from "vuex";
export default {
    data() {
        return {
            newPost: {
                title: "",
                picture: "",
                HTML: "",
                category: "",
                subcategory: { title: '', link: ''},
                shortcat: ""
            },
            mode: "create",
            infoAlert: "",
        };
    },
    computed: {
        ...mapGetters("СategoriesStore", ["categories"]),
        ...mapGetters("UsersStore", ["thisUser"]),
        categoriesOptions() {
            let resultOptions = [{ value: '', text: 'Не обрано'}];
            if (this.categories) {
                this.categories.map(category => {
                    resultOptions.push(
                        {
                            value: {
                                link: category.link,
                                title: category.title,
                            },
                            text: category.title
                        });
                });
            }
            return resultOptions;
        }
    },
    methods: {
        ...mapActions("PostsStore", [
            "addNewPost",
            "getAllPostsPreview",
            "editPost",
            "removePost"
        ]),
        ...mapActions("UsersStore", ["getThisUser"]),
        subcategoriesTitles(choosedCategory) {
            
            let resultOptions = [{ value: { title: '', link: ''}, text: "Без подрубрики" }];
            //console.log(categoryLink);
            if (choosedCategory) {
                const category = this.categories.find(category => category.link === choosedCategory.link);
                if (category.subcategories) {
                    category.subcategories.map(subCategory => {
                        console.log(subCategory);
                        resultOptions.push({
                            value: { title: subCategory.title, link: subCategory.link },
                            text: subCategory.title
                        });
                    });
                }
            }
            return resultOptions;
        },
        submitPost(e) {
            if (this.validatePost()) {
                const author = JSON.stringify(this.thisUser.name);
                const formData = new FormData();
                const data = JSON.stringify({
                    ...this.newPost,
                    categories: {
                        ...this.newPost.category,
                        subcategory: this.newPost.subcategory,
                    },
                    
                    author: this.thisUser,
                    date: Date.now()
                });
                formData.append("poster", this.newPost.picture);
                formData.append("data", data);
                console.log(data);
                // if we create new post
                if (this.mode === "create") {
                    
                    this.addNewPost(formData)
                    .then(() => {
                        this.showAlert('Ok!', 'Запись успешно добавлена!');
                        this.getAllPostsPreview();
                    });
                    
                } else if (this.mode === "edit") {
                    // if we edit existed post
                    this.editPost({ formData, id: this.newPost._id })
                    .then(() => {
                        this.showAlert('Ok!', 'Запись успешно отредактирована!');
                        this.getAllPostsPreview();
                        this.mode = "create";
                    });
                    
                }
                // reset form
                this.clearForm();
            }
        },
        choosePost(post) {
            this.newPost = {
                _id: post._id,
                title: post.title,
                category: { title: post.categories.title, link: post.categories.link },
                picture: post.picture,
                subcategory: post.categories.subcategory,
                HTML: post.content,
                shortcat: post.shortcat,
            };
            this.mode = "edit";
        },
        deletePost(id) {
            this.removePost(id).then(() => {
                this.getAllPostsPreview();
                this.mode = "create";
            });
        },
        clearForm() {
            this.newPost = {
                title: "",
                picture: "",
                HTML: "",
                category: "",
                subcategory: { title: '', link: ''},
                shortcat: ""
            };
            this.$refs.poster.reset();
        },
        validatePost() {
            let errMessage = '';
            const post = this.newPost;
            !post.title ? errMessage+= 'Введите заголовок новости!\n' : null;
            !post.picture ? errMessage+= 'Добавьте обложку новости!\n' : null;
            !post.HTML ? errMessage+= 'Введите содержимое новости!\n' : null;
            !post.category ? errMessage+= 'Укажите рубрику новости!\n' : null;
            !post.shortcat ? errMessage+= 'Добавьте шорткат новости!\n' : null;
            if (errMessage) {
                this.showAlert('Ошибка!', errMessage);
                return false;
            }
            return true;
        },
        showAlert(title, text) {
            this.infoAlert = text;
            this.$refs.alertModal.show();
            this.$refs.alertModal.title = title;
        }
    },
    components: {
        newsList: require("./newsList"),
    }
};
</script>

<style lang="sass" scoped>
.posts-panel
    padding: 40px 0
.editr
    overflow-x: hidden
</style>




