<template lang="pug">
    b-col(cols="12" md="6" order="1" order-md="2")
      b-list-group.categories-list
        b-list-group-item(
          v-for="category in categories"
          :key="category._id")
          .category-top
              h3.subtitle {{ category.title }}
              button(@click="deleteCategory(category)").close X
          b-list-group
            b-list-group-item(
              v-for="subcategory in category.subcategories"
              :key="subcategory._id")
              .subcategory-top
                h4.subtitle {{ subcategory.title }}
                button(@click="removeSubCategory(category, subcategory)").close X
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  props: {
    categories: Array
  },
  methods: {
    ...mapActions("СategoriesStore", [
      "getAllCategories",
      "editCategory",
      "removeCategory"
    ]),
    deleteCategory(category) {
      this.removeCategory(category._id)
        .then(response => {
          this.getAllCategories();
        })
        .catch(e => console.error(e));
    },
    removeSubCategory(category, subcatategory) {
      // filtering removing subcategory from his category
      category.subcategories = category.subcategories.filter(
        item => item._id !== subcatategory._id
      );
      this.editCategory(category)
        .then(response => {
          this.getAllCategories();
        })
        .catch(e => console.error(e));
    }
  }
};
</script>

<style lang="sass" scoped>
.categories-list
  height: 400px
  overflow-y: scroll
.category-top, .subcategory-top
    display: flex
    justify-content: space-between
    padding: 20px
.close  
    cursor: pointer
</style>



