import axios from 'axios';
const categoriesApi = {

    getAllCategories() {
        return axios.get('/api/allCategories')
            .then(response => response.data)
            .catch(e => { console.error(e); return []; });
    },

    addNewCategory(newCategory) {
        return axios.post('/api/addCategory', newCategory)
            .then(response => { console.log(response); return; })
            .catch(e => console.error(e));
    },

    editCategory(editedCategory) {
        return axios.put('/api/editCategory', editedCategory)
            .then(response => { console.log(response); return; })
            .catch(e => console.error(e));
    },

    removeCategory(id) {
        return axios.delete(`/api/removeCategory/${id}`)
            .then(response => { console.log(response); return; })
            .catch(e => console.error(e));
    },

};
export default categoriesApi;



