<template lang="pug">
    b-navbar(toggleable="md" type="dark" variant="info" tag="header").header
      b-navbar-toggle(target="nav_collapse")
      b-navbar-brand(to="/")
        img(src="~img/logo.png").logo
      span(v-if='thisUser') {{ thisUser.name }}
      b-collapse(is-nav id="nav_collapse")
        b-navbar-nav.nav
          li(v-for="item in categories" :key="item._id" class="nav__item")
            router-link(
              :to="'/'+item.link"
              class="nav__link"
              active-class="nav__link_active"
              ) {{item.title}}
            ul.nav__sub-list(v-if="item.subcategories.length")
              li(v-for="subitem in item.subcategories" :key="subitem._id" class="nav__sub-item")
                router-link(
                  :to="'/' + item.link + '/' + subitem.link"
                  class="nav__sub-link"
                  active-class="nav__sub-link_active"
                  ) {{subitem.title}}
        b-navbar-nav(class="ml-auto")
          b-dropdown(text='Вход' v-if="!thisUser" right style='width: 250px;')
            b-card
              b-form(@submit.prevent='signIn')
                b-alert(show v-if='success' variant='success') {{ info }}
                b-alert(show v-if='fail' variant='danger') {{ info }}
                b-form-group(label='Логин или E-mail')
                  b-form-input(
                    v-model='auth.userLogin'
                    placeholder='Ваш логин или пароль')
                b-form-group(label='Пароль')
                  b-form-input(
                    v-model='auth.userPassword'
                    type='password'
                    placeholder='Ваш пароль')
                b-button(type='submit') Авторизоваться
          b-button-group(v-else)
            router-link(
              to="/admin"
              class="btn btn-secondary"
              ) Добавить
            b-button(@click='signOut') Выйти
                
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      auth: {
        userLogin: '',
        userPassword: ''
      },
      success: false,
      fail: false,
      info: ''
    }
  },
  computed: {
    ...mapGetters("СategoriesStore", ["categories"]),
    ...mapGetters("UsersStore", ["thisUser"])
  },
  methods: {
    ...mapActions(['logIn', 'logOut']),
    signIn(e) {
      this.logIn(this.auth)
        .then((response => {
          this.info = response;
          this.fail = false;
          this.success = true;
          setTimeout(() => {
            this.$router.push('/admin');
          }, 1500);
        }))
        .catch(e => {
          this.info = e.message;
          this.fail = true;
          this.success = false;
        })
    },
    signOut() {
      this.logOut();
      this.$router.push('/');
    }
  }
};
</script>



