<template lang="pug">
    .container
        article.post
            .post__poster(:style="{ backgroundImage: 'url(' + post.picture + ')' }")
            h3.title.post__title {{ post.title }}
            time.post__date {{ dateParse(post.date) }}
            .post__content(v-html="post.content")
            .post__author {{ post.author }}
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import toDateString from '../jsModules/toDateString';
export default {
    computed: {
        ...mapGetters('PostsStore', ['posts']),
        post() {
            console.log(this.posts);
            return this.posts ? this.posts[0] : {};
        }
    },
    methods: {
        ...mapActions('PostsStore', ['getPostById']),
        dateParse(date) {
            return toDateString(date);
        }
    },
    created() {
        let id = this.$router.currentRoute.params.id;
        console.log('id', id);
        this.getPostById(id)
            .catch((e) => this.$router.redirect('404'));
    }
}
</script>

<style lang="sass" src="./style.sass" scoped></style>



