<template lang="pug">
    b-container
        b-row
            b-col(cols='12' md='6')
                b-form(@submit.prevent='sendInvite').mt-3
                    b-form-group(label='ФИО автора (никнейм)')
                        b-form-input(v-model='author.nick' placeholder='ФИО')
                    b-form-group(label='Email автора')
                        b-form-input(type='email' v-model='author.email' placeholder='example@com.ua')
                    b-button(type='submit') Отправить
            b-col(cols='12' md='6')
                b-list-group
                    b-list-group-item(v-for='user,i in users' :key='i')
                        p.subtitle {{ user.name }}
        b-modal(ref='infoModal')
            | {{ info }}
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
    data() {
        return {
            author: {
                nick: '',
                email: ''
            },
            info: ''
        }
    },
    computed: {
        ...mapGetters('UsersStore', ['users']),
    },
    methods: {
        ...mapActions('UsersStore', ['createAuthor', 'getAllUsers']),
        sendInvite () {
            this.createAuthor(this.author)
                .then(message => {
                    this.info = message;
                    this.$refs.infoModal.show()
                    this.author = {
                        nick: '',
                        email: ''
                    }
                })
                .catch(e => {
                    this.info = e.message;
                    this.$refs.infoModal.show()
                })
        }
    },
    created() {
        this.getAllUsers()
    }
}
</script>



