<template lang="pug">
    b-navbar(toggleable="md" type="dark" variant="secondary")
        b-navbar-toggle(target="nav_collapse")
        b-navbar-brand(href="#")
        b-collapse(is-nav id="nav_collapse")
            b-navbar-nav
                li(v-for="item in categories" :key="item._id" class="nav__item")
                    router-link(
                                :to="item.link"
                                class="nav__link"
                                active-class="nav__link_active"
                                ) {{item.title}}
</template>

<script>
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
        };
    },
    computed: {
        ...mapGetters('СategoriesStore', ['categories']),
    },
}
</script>

<style lang="sass" src="./style.sass" scoped></style>



