<template lang="pug">
    .container
        b-list-group
            b-alert(v-if="!postsToShow.length" show variant="secondary") Постов в данной рубрики еще нет!
            b-form-group( label="Выберите подрубрику:" class="mb-3 mt-3")
                b-form-select(
                    v-if="!subcategory"
                    v-model="selectedSubcategory"
                    :options="subcategoriesOptions"
                    )
            b-form-group( label="Сортировать по:" class="mb-3 mt-3")
                b-form-select(
                    v-model="selectedSort"
                    :options="sortOptions"
                    )
            b-list-group-item(v-for="post in postsToShow" :key="post._id" tag="article").post-item
                .post-item__picture
                    img(:src="post.picture" :alt="post.title").img-responsive
                h3.post-item__title {{ post.title }}
                .post-item__text {{ post.shortcat }}
                .post-item__date {{ dateParse(post.date) }}
                .post-item__author Автор: {{ post.author }}
                router-link(:to="category + '/' + (post.categories.subcategory.link || post.categories.link ) + '/' + post._id") Читать далее...
            b-btn(type="button" @click="showMore" v-if="postsToShow.length > toShow") Показать больше
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import toDateString from '../jsModules/toDateString';
export default {
    data() {
        return {
            category: this.$router.currentRoute.params.cat,
            subcategory: this.$router.currentRoute.params.subcat,
            toShow: 30,
            selectedSubcategory: '',
            selectedSort: '',
        };
    },
    computed: {
        ...mapGetters('СategoriesStore', ['categories']),
        ...mapGetters('PostsStore', ['posts']),
        postsToShow() {
            let resultPosts = this.posts;
            
            if (this.selectedSubcategory) {
                
                resultPosts = resultPosts
                    .filter(post => post.categories.subcategory.link === this.selectedSubcategory);
            }
            if (this.selectedSort) {
                switch(this.selectedSort) {
                    case 'new': resultPosts.sort((prev, next) => next.date - prev.date); break;
                    case 'old': resultPosts.sort((prev, next) => prev.date - next.date); break;
                }
            }
            return resultPosts.slice(0, this.toShow);
        },
        subcategoriesOptions() {
            const currentCategory = this.categories.filter(category => category.link === this.category);
            let options = [{ value: '', text: 'Все', selected: true }];
            currentCategory[0].subcategories.map(subcategory => {
                options.push({ value: subcategory.link, text: subcategory.title });
            });
            return options;
        },
        sortOptions() {
            return [
                { value: '', text: 'По умолчанию', selected: true },
                { value: 'new', text: 'Сначала новые' },
                { value: 'old', text: 'Сначала старые' },
            ]
        },
    },
    methods: {
        ...mapActions('PostsStore', ['getPostsByCategory']),
        showMore() {
            this.toShow += 30;
        },
        getPostsByCategoryOrSubcategory() {
            this.subcategory ? this.getPostsByCategory(this.subcategory) 
                : this.getPostsByCategory(this.category);
        },
        dateParse(date) {
            return toDateString(date);
        }
    },
    watch: {
        '$route' (to) {
            this.category = to.params.cat;
            this.subcategory = to.params.subcat;
            console.log(this.category, this.subcategory);
            this.getPostsByCategoryOrSubcategory();
        }
    },
    created() {
        this.getPostsByCategoryOrSubcategory();
    },
}
</script>

<style lang="sass" src="./style.sass" scoped></style>


