<template lang="pug">
    b-list-group.news-list
      b-list-group-item(
        v-for="post in posts"
        :key="post.date"
        :style="{ backgroundImage: 'url(' + post.picture + ')' }"
        @click.stop="choosePost(post)"
        ).news-list__item
        .news-list__row
          h3.title {{ post.title }}
          button(@click.stop="deletePost(post._id)").close X
        .news-list__row
          time.subtitle {{ dateParse(post.date) }}
          p.subtitle {{ post.author }}
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import toDateString from "../../jsModules/toDateString";
export default {
  methods: {
    ...mapActions("PostsStore", ["getAllPostsPreview"]),
    choosePost(post) {
      this.$emit("choosePost", post);
    },
    deletePost(id) {
      this.$emit("deletePost", id);
    },
    dateParse(date) {
      return toDateString(date);
    }
  },
  computed: {
    ...mapGetters("PostsStore", ["posts"])
  },
  created() {
    this.getAllPostsPreview();
  }
};
</script>
<style lang="sass" scoped>
.news-list
    max-height: 440px
    height: 100%
    overflow-x: hidden
    overflow-y: scroll
    &__item
        filter: grayscale(100%)
        filter: grayscale(1)
        color: #ffffff
        background-size: cover
        background-position: center
</style>



